

export default function UserData({ avatar, username, tag, location, followers, views, likes }) {
  return  <div>
  <div>
    <img
      src={avatar}
      alt={"avatar"} width= '300'
      
    />
      <p >{ username}</p>
      <p>{tag}</p>
      <p>{location}</p>
  </div>

  <ul>
    <li>
        <span>Followers</span>
        <span>{followers}</span>
    </li>
    <li>
      <span>Views</span>
      <span>{views}</span>
    </li>
    <li>
      <span>Likes</span>
      <span>{likes}</span>
    </li>
  </ul>
</div>
}